import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { IPsLookupOwnPayIdsComponentOptionsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-payIds/ps-lookup-own-payIds.component.interfaces';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { IMapKeyValue } from "src/app/commonSRC/psServices/models/ps-common-interface";
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsPayIdBeneficiaryComponent } from "../../ps-beneficiary/ps-payId-beneficiary/ps-payId-beneficiary.component";
import { LoggerService } from './../../../../commonSRC/psServices/logger/logger.service';
import { OmniPullService } from './../../../psServices/omni-common/omni-pull.service';
import { IOptionsPsComplexLookUpComponentExposed } from './../../ps-complex-components/ps-complex-lookup/ps-complex-lookup.component.interfaces';


@Component({
  selector: 'ps-lookup-own-payIds',
  templateUrl: './ps-lookup-own-payIds.component.html',
  styleUrls: ['./ps-lookup-own-payIds.component.scss'],
})
export class PsLookupOwnPayIdsComponent extends PsBaseFieldComponent implements OnInit {
  @Input() options: IPsLookupOwnPayIdsComponentOptionsExposed = {
    requestObject: null
  }

  public complexLookupOptions: IOptionsPsComplexLookUpComponentExposed = {
    requestObject: null,
    notFoundMessage: 'no_payids_found_key'
  };
  public labelValuesMap = new Map<string, IMapKeyValue>();
  @ViewChild('complexLookupComponentObj') complexLookupComponentObj;


  constructor(public common: PsCommonService, public logger: LoggerService, public omniPull: OmniPullService) {
    super(common, logger);
    this.populatePayIdMap();
  }

  ngOnInit() {
    super.init();
    this.returnPayIdList();
    this.complexLookupOptions.title = 'title_key';
    this.complexLookupOptions.subTitle = 'sub_title_key';
    this.complexLookupOptions.labelsValueMap = this.labelValuesMap;
    this.common.copyObject(this.complexLookupOptions, this.options, false);
    this.complexLookupOptions.requestObject = this.options.requestObject;
    if (this.options && this.options.component) {
      this.complexLookupOptions.component = this.options.component;
    } else {
      this.complexLookupOptions.component = PsPayIdBeneficiaryComponent;
    }
  }

  async returnPayIdList() {
    const beneficiariesRequestParam = {
      filterByOper: true,
      fromTo: this.options.fromTo
    };

    const result: any = await this.omniPull.returnBeneficiaries(beneficiariesRequestParam).catch(error => {
      this.complexLookupOptions.listOfOptions = [];
      this.logger.error('Error: While fetching payID beneficiaries in PsLookupBeneficiariesLocalComponent :', error);
    });
    //demo
    if (this.options.fromTo === 'to') {
      result.gridModel = [{
        submitFieldValueMap: {
          nickName: 'IBA Wallet',
          linkedAccount: '00018000132160353140272',
          payIdType: 'P',
          status: 'Active',
          payIdTypeDescriptionConcat: 'Phone Number',
          phoneNumber: '376712345',
          lookupKey: '1',
          purpose: 'business wallet',
          previewProperty: 'nickName',
          statusCode: 'A'
        },
        dataSaveId: 1
      },
      {
        submitFieldValueMap: {
          nickName: 'NAB Wallet',
          linkedAccount: '00018000132160353140272',
          payIdType: 'P',
          status: 'Active',
          payIdTypeDescriptionConcat: 'Email Address',
          emailaddress: 'payID2@azentio.com',
          lookupKey: '2',
          purpose: 'business wallet',
          previewProperty: 'nickName',
          statusCode: 'A'
        },
        dataSaveId: 2
      },
      ]
    } else {
      result.gridModel = [{
        submitFieldValueMap: {
          nickName: 'Ahmed ElFkhrany',
          availableBalance: '2000000',
          linkedAccount: '00018000132160353140272',
          payIdType: 'P',
          status: 'Active',
          payIdTypeDescriptionConcat: 'Phone Number',
          phoneNumber: '376712345',
          lookupKey: '00018000132160353140272',
          purpose: 'Testing',
          previewProperty: 'nickName',
          statusCode: 'A'
        }
      },
      {
        submitFieldValueMap: {
          nickName: 'Mohamed Fawzy',
          availableBalance: '111745',
          linkedAccount: '00018000132100353140275',
          payIdType: 'P',
          status: 'Active',
          payIdTypeDescriptionConcat: 'Mobile Number',
          mobileNumber: '911606588589',
          lookupKey: '00018000132100353140275',
          purpose: 'Testing',
          previewProperty: 'nickName',
          statusCode: 'A'
        }
      },
      {
        submitFieldValueMap: {
          nickName: 'Muhammad Romani',
          availableBalance: '2000',
          linkedAccount: '00018000132160353140272',
          payIdType: 'E',
          status: 'Deactive',
          payIdTypeDescriptionConcat: 'Email Address',
          emailaddress: 'mohammadromani@azentio.com',
          lookupKey: 'MR1300018000132160353140272',
          purpose: 'Testing',
          previewProperty: 'nickName',
          statusCode: 'B'
        }
      }
      ]
    }

    if (result && result.gridModel.length > 0) {
      this.complexLookupOptions.listOfOptions = this.populatePayIdsList(result.gridModel);
    } else {
      this.complexLookupOptions.listOfOptions = [];
    }
  }

  populatePayIdsList(dataList) {
    const payidList = [];
    for (let item of dataList) {

      if (item && item.submitFieldValueMap) {
        item = item.submitFieldValueMap;
        const payidCard = {
          nickName: item.nickName,
          payIdType: item.payIdType,
          payIdTypeDescriptionConcat: item.payIdTypeDescriptionConcat,
          payIdContent: item.payIdTypeDescriptionConcat + ' - ' + (item.phoneNumber || item.emailAddress || item.mobileNumber),
          phoneNumber: item.phoneNumber,
          emailaddress: item.emailAddress,
          mobileNumber: item.mobileNumber,
          purpose: item.purpose,
          lookupKey: item.nickName,
          previewProperty: 'nickName',
        }
        payidList.push(payidCard);
      }
    }
    return payidList;
  }

  onSelectItem(event) {

  }

  private populatePayIdMap() {
    let payIdContentMap: IMapKeyValue = {};

    payIdContentMap = {
      key: 'payIdContent',
      value: '------',
      isEdit: false
    };

    // key here will be used as label in look up details component
    this.labelValuesMap.set('payid_key', payIdContentMap);
  }

  reset() {
    this.complexLookupComponentObj.resetLookUp();
  }

}
